<template>
  <div>
    <v-container>
      <v-layout row fill-height justify-center>
        <v-flex md6 justify-center class="text-xs-center" style="padding-top: 80px;">
          <input type="file" ref="fileInput" v-on:change="onFileChange" style="display: none"/>
          <div class="display-2 mb-4">Pilih CSV untuk diimport</div>
        </v-flex>
      </v-layout>
      <v-layout row fill-height justify-center>
        <v-flex md4 justify-center class="text-xs-center">
          <v-btn large dark color="cyan" @click="test">
            Pilih File
          </v-btn>
          <v-btn large dark color="cyan" @click="saveData">
            Simpan
          </v-btn>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import evbus from "@/evbus";
import { Cuaca__imports } from "@/services/cuaca";

export default {
  name: 'CsvImport',
  data () {
    return {
      fileProcessed: false,
      file: undefined,
      isSaving: false,
      isError: false
    }
  },
  methods: {
    test () {
      this.$refs.fileInput.click()
    },
    async saveData () {
      let file = this.file
      if (file === undefined || file === null) {
        return 
      }

      this.error = false
      this.isSaving = true
      evbus.$emit("loading-on");

      let formData = new FormData()
      formData.append('data', file, 'data')

      try {
        await Cuaca__imports(formData)
        evbus.$emit("success", "Sukses mengimport data");
      } catch (err) {
        this.error = true;
        evbus.$emit("error", "Gagal mengimport data");
        console.log(err);
      } finally {
        this.isSaving = false;
        evbus.$emit("loading-off");
      }
    },
    onFileChange (e) {
      var files = e.target.files || e.dataTransfer.files
      if (!files.length) {
        return
      }
      this.file = files[0]
    }
  }
}
</script>

