import axios from "./axios";
import moment from "moment";
import { CuacaPerHari } from "@/models/CuacaPerHari";

export async function Cuaca__create(payload: CuacaPerHari) {
  let _payload: any = { ...payload };
  _payload.tanggal = moment(payload.tanggal, "YYYY-MM-DD");
  const response = await axios.post("/cuaca", _payload);
  return response.data;
}

export async function Cuaca__find() {
  const response = await axios.get('/cuaca');
  return response.data;
}

export async function Cuaca__findOne(id: string) {
  const response = await axios.get(`/cuaca/${id}`);
  return response.data;
}

export async function Cuaca__update(id: string, payload: CuacaPerHari) {
  let _payload: any = { ...payload };
  delete _payload._id;
  _payload.tanggal = moment(payload.tanggal, "YYYY-MM-DD");
  const response = await axios.put(`/cuaca/${id}`, _payload);
  return response.data;
}

export async function Cuaca__delete(id: string) {
  await axios.delete(`/cuaca/${id}`);
}

export async function  Cuaca__imports(fd: FormData) {
  const response = await axios.post(`/imports`, fd);
  return response;
}
